.home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    margin-top: 40px;
}
.wrap {
    display: flex;
    flex-wrap: wrap;
    /* margin-right: 20%;
    margin-left: auto; */
    width: 60%;
}

.wrap .product {
    width: calc(33.333% - 6px);
    height: auto;
    margin: 3px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 2px 5px 7px rgba(0, 0, 0, 0.2);
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;
}
.wrap .product .thumb {
    width: 100%;
    /* border: black solid 1px; */
    border-radius: 10px 10px 0px 0px;
}
.wrap .product .name {
    font-size: 18px;
    padding-left: 10px;
    margin: 0;
    /* text-align: center; */
}
.wrap .product .desc {
    font-size: 14px;
    padding-left: 10px;
    padding-right: 10px;
    padding-top: 2px;
    margin: 0;
    text-align: justify;
    overflow: hidden;
    display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
    /* text-align: center; */
}
.wrap .product .see {
    font-size: 18;
    text-decoration: none;
    color: #718093;
    font-weight: bolder;
    width: 90%;
    text-align: center;
    padding: 10px;
    margin-bottom: 5px;
    margin-top: auto;
}
hr {
    width: 70%;
    height: 0.1px;
    color: #2f36402f;
    background-color: #2f36402f;

}
@media only screen and (max-width: 600px){
    .wrap {
        width: 100%;
        padding-bottom: 40px;
    }
    .wrap .product {
        width: 100%;
        margin-right: 15%;
        margin-left: 15%;
    }
}